import React, { Component } from 'react'


export default class Todolist extends Component {
    render() {
        return (
            <div>
                <div style={{ width: '100%', height: '80px', background: 'gray', textAlign: 'center' }}>
                    <div>
                        <h1>ToDoList</h1>
                    </div>
                    <div style={{ marginTop: '10px', color: 'white' }}>
                        <p>最简单的待办事项列表</p>
                    </div>
                </div>
            </div>
        )
    }
}