import React, { Component } from 'react'
const upd = document.getElementsByClassName("show1");
const downd = document.getElementsByClassName("show2");

export default class Todoing extends Component {
    constructor() {
        super();
        this.state = {
            a: 0,
            b: 0,
            todo: []
        }
    }
    handleInput = (e) => {
        // 绑定this，事件处理函数写成箭头函数，或者用bind
        let str = e.target.value.trim();
        if (e.keyCode === 13 && str != '') {
            let str1 = JSON.parse(localStorage['todo']);
            str1.push({
                title: str,
                done: false
            })
            this.setState({
                a: this.state.a + 1
            })
            let newtg = document.createElement("div");
            newtg.setAttribute("class", "inp");
            newtg.innerHTML += "<input type='checkbox' class='ckbox'/><button class='but'>一</button><div style=display:'block'>" + e.target.value + "</div>";
            newtg.childNodes[0].onclick = this.chinp;
            newtg.childNodes[1].onclick = this.rmdiv;
            localStorage.setItem('todo', JSON.stringify(str1));
            var show = e.target.parentNode.parentNode.childNodes[2];
            show.appendChild(newtg);
            e.target.value = '';
        }
    }
    rmdiv = (e) => {
        let str1 = JSON.parse(localStorage['todo']);
        let cd = e.target.parentNode;
        let cdpar = cd.parentNode;
        cdpar.removeChild(cd);
        for (let i = 0; i < str1.length; i++) {
            if (str1[i].title == cd.childNodes[2].innerHTML) {
                str1.splice(i, 1);
            }
        }
        localStorage.setItem('todo', JSON.stringify(str1));
        if (cd.childNodes[0].checked === false) {
            // console.log(cd.childNodes[2].innerHTML);
            this.setState({
                a: cdpar.childNodes.length
            })
        } else {
            this.setState({
                b: cdpar.childNodes.length
            })
        }
    }
    chinp = (e) => {
        // console.log(e.target.parentNode.childNodes[2].innerHTML);
        if (e.target.checked === true) {
            let newDom = e.target.parentNode;
            newDom.className = 'inped';
            newDom.parentNode.parentNode.childNodes[4].appendChild(newDom);
            let str1 = JSON.parse(localStorage['todo']);
            for (let i = 0; i < str1.length; i++) {
                if (str1[i].title == e.target.parentNode.childNodes[2].innerHTML) {
                    str1[i].done = true;
                }
            }
            localStorage.setItem('todo', JSON.stringify(str1));
            this.setState({
                a: this.state.a - 1,
                b: this.state.b + 1
            })
        } else {
            let newDom = e.target.parentNode;
            newDom.className = 'inp';
            newDom.parentNode.parentNode.childNodes[2].appendChild(newDom);
            let str1 = JSON.parse(localStorage['todo']);
            for (let i = 0; i < str1.length; i++) {
                if (str1[i].title == e.target.parentNode.childNodes[2].innerHTML) {
                    str1[i].done = false;
                }
            }
            localStorage.setItem('todo', JSON.stringify(str1));
            this.setState({
                a: this.state.a + 1,
                b: this.state.b - 1
            })
        }
    }
    componentDidMount() {
        // console.log(this.state.todo);
        // console.log(upd[0]);
        if (localStorage["todo"]) {
            let a = 0;
            let b = 0;
            let str1 = JSON.parse(localStorage['todo']);
            // console.log(str1[0].title);
            for (let i = 0; i < str1.length; i++) {
                if (str1[i].done == false) {
                    a += 1;
                    this.setState({
                        a: a
                    })
                    let newtg = document.createElement("div");
                    newtg.setAttribute("class", "inp");
                    newtg.innerHTML += "<input type='checkbox' class='ckbox'/><button class='but'>一</button><div style=display:'block'>" + str1[i].title + "</div>";
                    newtg.childNodes[0].onclick = this.chinp;
                    newtg.childNodes[1].onclick = this.rmdiv;
                    upd[0].appendChild(newtg);
                } else {
                    b += 1;
                    this.setState({
                        b: b
                    })
                    let newtg = document.createElement("div");
                    newtg.setAttribute("class", "inped");
                    newtg.innerHTML += "<input type='checkbox' class='ckbox'/><button class='but'>一</button><div style=display:'block'>" + str1[i].title + "</div>";
                    newtg.childNodes[0].onclick = this.chinp;
                    newtg.childNodes[0].checked = true;
                    newtg.childNodes[1].onclick = this.rmdiv;
                    downd[0].appendChild(newtg);
                }
            }
        }
    }
    render() {
        return (
            <div>
                <div style={{ width: '100%', height: '30px', textAlign: 'center', margin: '10px auto' }}>
                    <input onKeyDown={this.handleInput} style={{ width: '200px', height: '20px' }} type="text" placeholder="请在此输入您的待办事项" />
                </div>
                <div style={{ width: '100%', height: '50px' }}>
                    <div style={{ width: '500px', height: '50px', margin: '10px auto' }}>
                        <div style={{ float: 'left', display: 'block', fontWeight: 'bold', fontSize: '20px' }}>
                            正在进行
                    </div>
                        <div style={{ margin: '0 auto', textAlign: 'center', overflow: 'hidden', float: 'right', display: 'block', width: '24px', height: '24px', borderRadius: '12px', background: 'black', color: 'white' }}>
                            <span style={{ fontSize: '14px' }}>{this.state.a}</span>
                        </div>
                    </div>
                </div>
                <div className="show1">

                </div>
                <div style={{ width: '100%', height: '50px' }}>
                    <div style={{ width: '500px', height: '50px', margin: '10px auto' }}>
                        <div style={{ float: 'left', display: 'block', fontWeight: 'bold', fontSize: '20px' }}>
                            已经完成
                    </div>
                        <div style={{ margin: '0 auto', textAlign: 'center', overflow: 'hidden', float: 'right', display: 'block', width: '24px', height: '24px', borderRadius: '12px', background: 'black', color: 'white' }}>
                            <span style={{ fontSize: '14px' }}>{this.state.b}</span>
                        </div>
                    </div>
                </div>
                <div className="show2">

                </div>
            </div>
        )
    }
}
